  padding-right: 24px;
  padding-left: 24px;
  transform: translate(-50%, 100%);
  transition: transform 0.25s 0ms cubic-bezier(0.4, 0, 1, 1);
  background-color: #323232;
  pointer-events: none;
  will-change: transform; }
  @media (max-width: 599px) {
    .mdc-snackbar {
      left: 0;
      width: 100%;
      transform: translate(0, 100%); } }
  @media (min-width: 600px) {
