  .mdc-snackbar[dir="rtl"] .mdc-snackbar__text,
  [dir="rtl"] .mdc-snackbar .mdc-snackbar__text {
    /* @noflip */
    margin-left: auto;
    /* @noflip */
    margin-right: 0; }
  @media (min-width: 600px) {
